import './timeline.scss'


function Timeline({ light }) {
    return (
        <div className='timeline-container pr'>
            <div className="timeline-pri">
                <div className="timeline">
                    {/* <div className="container right">
                    <div className={light ? "content content-light" : "content"}>
                        <p className={light ? "timelineYear timelineYear-light" : "timelineYear"}>2002</p>
                        <p className={light ? "timelineText timelineText-light" : "timelineText"}>My birth year</p>
                    </div>
                </div> */}
                    <div className="container right">
                        <div right>
                            <div className={light == "light" ? "content content-light" : "content"} >
                                <p className={light == "light" ? "timelineYear timelineYear-light" : "timelineYear"}>~2018</p>
                                <p className={light == "light" ? "timelineText timelineText-light" : "timelineText"}>Dharam Deep Sec. Public School</p>
                                <p className={light == "light" ? "timelineTagLine timelineTagLine-light" : "timelineTagLine"}>10<sup>th</sup> grade</p>
                            </div>
                        </div>
                    </div>
                    <div className="container left">
                        <div left>
                            <div className={light == "light" ? "content content-light" : "content"}>
                                <p className={light == "light" ? "timelineYear timelineYear-light" : "timelineYear"}>2018-2020</p>
                                <p className={light == "light" ? "timelineText timelineText-light" : "timelineText"}>Shri Daulat Ram Sr. Sec. Public School</p>
                                <p className={light == "light" ? "timelineTagLine timelineTagLine-light" : "timelineTagLine"}>12<sup>th</sup> grade from science stream.</p>
                            </div>
                        </div>
                    </div>
                    <div className="container right">
                        <div right>
                            <div className={light == "light" ? "content content-light" : "content"}>
                                <p className={light == "light" ? "timelineYear timelineYear-light" : "timelineYear"}>2020-2024</p>
                                <p className={light == "light" ? "timelineText timelineText-light" : "timelineText"}>Maharshi Dayanand University</p>
                                <p className={light == "light" ? "timelineTagLine timelineTagLine-light" : "timelineTagLine"}>B.Tech CSE</p>
                            </div>
                        </div>
                    </div>
                    <div className="container left">
                        <div left>
                            <div className={light == "light" ? "content content-light" : "content"}>
                                <p className={light == "light" ? "timelineYear timelineYear-light" : "timelineYear"}>Present</p>
                                <p className={light == "light" ? "timelineText timelineText-light" : "timelineText"}>Web Development</p>
                                <p className={light == "light" ? "timelineTagLine timelineTagLine-light" : "timelineTagLine"}>React.js</p>
                            </div>
                        </div>
                    </div>


                </div>
            </div>
        </div>



    )
}

export default Timeline;